.container{
    text-align: center;
}

.title{
    margin-top: 50px;
    color: white;
    font-size: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.reset{
    width: 250px;
    height: 100px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 50px;
    background: #1f3540;
    font-size: 26px;
    color: #26ffcb;
    margin-top: 20px;
} 

.boxes{
    display: flex;
    height: 120px;
    width: 120px;
    background: #1f3540;
    border: 4px solid #0f1b21;
    border-radius: 12px;
    cursor: pointer;
}

.board{
      height: 400px;
      width: 564px;
      display: flex;
      justify-content:center;
      margin: auto;
}

.boxes img{
    margin: 30px;
}

.title img{
    padding: 0px 20px 0px 20px;
    height: 50px;
}